.profile {
  margin: 0 auto;
  text-align: center;
  width: 300px;
  overflow: hidden;
  border-radius: 5px;
  color: rgb(128, 102, 102);
  border: 1px solid rgb(209, 209, 209);
  box-shadow: 2px 4px 4px 2px rgba(187, 187, 187, 0.3);
}

.profile__description {
  padding: 20px;
  font-size: 16px;
  line-height: 10px;
}

.profile__avatar {
  width: 100px;
  margin: 0 auto;
}

.profile__name {
  font-size: 25px;
  font-weight: 600;
  color: rgb(107, 85, 85);
}

.profile__location {
  font-weight: 500;
}

.profile__stats {
  display: flex;
  align-items: center;
  justify-items: center;
  font-size: 14px;
  border-top: 1px solid rgb(209, 209, 209);
}

.profile__stats-item {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding: 20px;
  width: calc(100% / 3);
  height: 80px;

  background-color: rgb(238, 238, 238);
}

.profile__stats-item:not(:first-child) {
  border-left: 1px solid rgb(209, 209, 209);
}

.profile__stats-label {
  display: block;
}

.profile__stats-quantity {
  display: block;
  font-weight: 700;
  color: rgb(107, 85, 85);
}
